<template>
    <div>
        <v-sheet color="rgba(0,0,0,0.7)" class="white--text text-center">
             <v-container >
                <v-row>
                  <v-col cols="12">
                      <p class="text-h4">RETARDOS</p>
                  </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-data-table
    :headers="headers"
    :items="desserts"
    sort-by="Fecha"
    class="elevation-1"
    dark
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >            
        <v-dialog v-model="dialog" max-width="500px" >
          <v-card class="white--text text-center" dark>
            <v-card-title class="text-h5">Aceptar Retardo {{editedItem.Tipo}}</v-card-title>
            <v-card-text>
                <v-col cols="12" >
                    <p>Nombre: {{editedItem.Nombre}}</p>
                </v-col>
                <v-col cols="12" >
                    <p>Tipo De Retardo {{editedItem.Tipo}}</p>
                </v-col>
                <v-col cols="12" >
                    <p>Fecha De Incidencia: {{editedItem.Fecha}}</p>
                </v-col>
                <v-col cols="12" >
                    <p>Tiempo De Retardo: {{editedItem.Tiempo}} min</p>
                </v-col>
                <v-col cols="12" >
                    <p>Retardos del mismo tipo: {{editedItem.Tiempo}}</p>
                </v-col>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>        
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>      
    </template>    
  </v-data-table>
                    </v-col>
                </v-row>
              </v-container>
        </v-sheet>
        
        
    </div>
</template>
<script>
export default {
    name:"Reporte_Retardo",
    data: () => ({
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: 'Nombre',
          align: 'start',
          sortable: false,
          value: 'Nombre',
        },
        { text: 'DÃ­a', value: 'Fecha' },
        { text: 'Tiempo De Retardo', value: 'Tiempo' },
        { text: 'Tipo De Retardo', value: 'Tipo' },        
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        Nombre: '',
        Fecha: 0,
        Tiempo: 0,
        Tipo: 0,
        Numero_Retardos: 0,
      },
      defaultItem: {
        Nombre: '',
        Fecha: 0,
        Tiempo: 0,
        Tipo: 0,
        Numero_Retardos: 0,
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.desserts = [
          {
            Nombre: 'Luis',
            Fecha: "12-12-12",
            Tiempo: 5,
            Tipo: "Mayor",
            Numero_Retardos: 0,
          },
          {
            Nombre: 'Luis Manuel',
            Fecha: "12-12-12",
            Tiempo: 10,
            Tipo: "Mayor",
            Numero_Retardos: 0,
          },
          
        ]
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
}
</script>